  align-items: center;
  min-height: 3.75rem;
  padding: 0.65rem 0;
  background-color: #fff;
}
.navbar > .wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
}
.navbar-logo {
  display: inline-block;
  color: inherit;
}
.navbar.fixed-top {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
}
.navbar.sticky-top {
  position: sticky;
  top: 0;
  right: 0;
  left: 0;
  z-index: 999;
}

.heading-xl, .heading-lg, .heading-md, .heading-sm, .heading-xs {
  margin-top: 0;
  margin-bottom: 1rem;
  font-weight: 500;
  line-height: 1.2;
  letter-spacing: -0.4px;
}

.heading-xs {
  font-size: 1.25rem;
}
.heading-sm {
  font-size: 1.5rem;
}
.heading-md {
  font-size: 1.75rem;
}
.heading-lg {
  font-size: 2rem;
}
.heading-xl {
  font-size: 2.5rem;
}

.text-left {
  text-align: left;
}

.text-center {
  text-align: center;
}

.text-right {
  text-align: right;
}

.text-justify {
  text-align: justify;
